* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: grey;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}
.list__item {
  display: flex;
  margin-bottom: 5px;
  height: 25px;
  align-items: center;
  font-weight: 400;
}

.list__item img {
  margin-right: 5px;
}

.country-info__head {
  display: flex;
  align-items: center;
}
.country-info img {
  width: 50px;
  margin-right: 5px;
}
.country-info__key {
  font-weight: bold;
}
